import { NextRequest, NextResponse } from "next/server"
import { prisma } from "@/lib/prisma"

// ===== GET BUREAU PAR ID (Avec inscrits + votants + centre + commune + wilaya) =====
export async function GET(
  request: NextRequest,
  { params }: { params: { id: string } }
) {
  try {
    const bureauId = Number(params.id)

    if (isNaN(bureauId)) {
      return NextResponse.json({ error: "Invalid bureau id" }, { status: 400 })
    }

    const bureau = await prisma.bureau.findUnique({
      where: { id: bureauId },
      include: {
        centre: {
          include: {
            commune: {
              include: {
                moughataa: {
                  include: { wilaya: true }
                }
              }
            }
          }
        }
      }
    })

    return NextResponse.json(bureau)
  } catch (e) {
    console.error("ðŸ“¥ GET /bureaux/[id] ERROR:", e)
    return NextResponse.json({ error: "Server error" }, { status: 500 })
  }
}


// ===== UPDATE VOTANTS =====
export async function PUT(
  req: NextRequest,
  { params }: { params: { id: string } }
) {
  try {
    const bureauId = Number(params.id)
    const { nombreVotants } = await req.json()

    const bureau = await prisma.bureau.update({
      where: { id: bureauId },
      data: { nombreVotants: Number(nombreVotants) }
    })

    return NextResponse.json({ success: true, bureau })
  } catch (e) {
    console.error("ðŸ“¥ PUT /bureaux/[id] ERROR:", e)
    return NextResponse.json({ error: "Server error" }, { status: 500 })
  }
}
